//
// Color
//

// basic
$color-primary-dark                   : rgb(187, 068, 058); // #bb443a
$color-primary                        : rgb(234, 085, 072); // #ea5548
$color-primary-light                  : rgb(249, 204, 200); // #f9ccc8

$color-green-dark                     : rgb(049, 105, 000); // #316901
$color-green-light                    : rgb(181, 226, 084); // #b5e254

$color-danger                         : rgb(250, 000, 000); // #fa0000

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(051, 051, 051); // #333333
$gray-darker                          : rgb(065, 065, 065); // #414141
$gray-dark                            : rgb(096, 096, 096); // #606060
$gray                                 : rgb(110, 110, 110); // #6e6e6e
$gray-light                           : rgb(172, 172, 172); // #acacac
$gray-lighter                         : rgb(234, 234, 234); // #eaeaea
$gray-lightest                        : rgb(252, 252, 252); // #fcfcfc
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "Lato", "system", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;

@mixin full-fill($position: absolute) {
  position: $position;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@mixin image-fluid {
  max-width: 100%;
  height: auto;
}

@mixin image-centre($height, $size: "contain", $child: "img") {
  height: $height;
  line-height: $height;
  text-align: center;
  overflow: hidden;

  > #{$child} {
    @include image-centre-child($size);
  }
}

@mixin image-centre-child($size: "contain") {
  margin: -100%;

  @if $size == "contain" {
    max-height: 100%;
    max-width: 100%;
  } @else if $size == "cover-by-width" {
    min-width: 100%;
  } @else if $size == "cover-by-height" {
    min-height: 100%;
  }
}

@mixin position-centre($position: absolute) {
  position: $position;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin position-center-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%) #{$transform};
}

@mixin position-center-over-right($transform...) {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(100%, -50%) #{$transform};
}

@mixin position-center-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%) #{$transform};
}

@mixin position-center-over-left($transform...) {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%) #{$transform};
}

@mixin position-top-over-left($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-top-over-right($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin position-right-over-top($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-right-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-left-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-left-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-bottom-over-left($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-bottom-over-right($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

/**
 * Pure CSS text truncate
 * See [Line Clampin' (Truncating Multiple Line Text) - Weird WebKit Flexbox Way](https://css-tricks.com/line-clampin/)
 */

@mixin text-truncate-singleline($width: 100%) {
  // Requires inline-block or block for proper styling

  width: $width;
  white-space: nowrap;
  @include text-truncate-style;
}

@mixin text-truncate-multiline($line-to-show, $line-height: 1.5, $font-size: 1em) {
  display: block; // fallback
  display: -webkit-box;
  line-height: $line-height;
  max-height: $font-size * $line-height * $line-to-show; // fallback
  -webkit-line-clamp: $line-to-show;
  @include text-truncate-style;

  // *** IMPORTANT SEE ***:
  // 1. https://github.com/postcss/autoprefixer/issues/776#issuecomment-392346351
  // 2. https://github.com/NoName4Me/yo-FE/issues/3
  // eslint-disable-next-line
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}

@mixin text-truncate-style {
  overflow: hidden;
  text-overflow: ellipsis;
}

.task-analytics {
  &__panel {
    border-radius: 2px;
    background-color: $gray-darker;
  }

  &__row {
    padding: 15px 0;
    display: flex;
  }

  &__column {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:first-of-type {
      border-right: 1px solid $gray;
    }
  }

  &__chart-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__chart {
    height: 100%;
    display: flex;
    align-items: flex-end;
  }

  &__grid {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }

  &__count-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 25px;
  }

  &__count {
    margin-top: 1px;
    display: inline-block;
    height: 8px;
    border-radius: 5px;
    background-color: $color-primary;
  }

  &__week {
    margin-top: 10px;
  }

  &__date {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__navigation-wrapper {
    margin: 30px 0;
    display: flex;
    justify-content: space-between;
  }
}
